  .swiper {
    max-width: 566px;
  }
}
@media (max-width: 767px) {
  .swiper {
    max-width: 226px;
  }
}

.swiper__back-button {
  position: absolute;
  transform: translateY(-50%) rotate(180deg);
  top: 50%;
  left: 0;
  border: none;
  background-color: transparent;
  z-index: 10;
  cursor: pointer;
  padding: 0;
  margin-left: 47px;
}
@media (max-width: 767px) {
  .swiper__back-button {
    margin-left: 15px;
  }
}

.swiper__next-button {
  position: absolute;
  transform: translateY(-50%);
  top: 50%;
  right: 0;
  border: none;
  background-color: transparent;
  z-index: 10;
  cursor: pointer;
  padding: 0;
  margin-right: 47px;
}
@media (max-width: 767px) {
  .swiper__next-button {
    margin-right: 15px;
  }
}

.feedback .swiper__next-button {
  right: -10%;
  margin: 0;
}

.feedback .swiper__back-button {
  left: -10%;
  margin: 0;
}

.swiper-slide {
  width: fit-content;
  width: auto;
}

.swiper-slide__coach-container {
  width: fit-content;
}

.swiper-slide__coach-photo {
  display: block;
  margin: 0;
  width: 100%;
}
@media (max-width: 1199px) {
  .swiper-slide__coach-photo {
    object-fit: cover;
    width: 268px;
    height: 294px;
  }
}
@media (max-width: 767px) {
  .swiper-slide__coach-photo {
    width: 226px;
    height: 274px;
  }
}

.swiper-slide__coach-container p {
  margin: 0;
  bottom: 6%;
  left: 50%;
  transform: translate(-50%, -50%);
  position: absolute;
  color: #ffffff;
  font-size: 18px;
  font-weight: 700;
  line-height: 18px;
  width: max-content;
  max-width: 260px;
  text-align: center;
  z-index: 10;
}
@media (max-width: 1199px) {
  .swiper-slide__coach-container p {
    left: 52%;
  }
}

.swiper-slide__additional-information {
  display: none;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: rgba(16, 38, 84, 0.89);
  cursor: pointer;
  transition: 1s;
}

.swiper-slide__additional-information span {
  display: block;
  margin: 81px 0 35px;
  text-align: center;
  color: #b6bdcc;
  font-size: 16px;
  font-weight: 400;
  line-height: 16px;
}

.swiper-slide__additional-information ul {
  margin: 0 auto;
  padding: 0 10px 0 71px;
}

.swiper-slide__additional-information li {
  color: #ffffff;
  color: #ffffff;
  font-size: 14px;
  font-weight: 400;
  line-height: 150%;
  margin-bottom: 13px;
}

.swiper-slide__additional-information li:last-child {
  margin-bottom: 0;
}

.swiper-slide__coach-container:hover .swiper-slide__additional-information {
  display: block;
}

.swiper-slide__coach-container:hover p {
  bottom: 72%;
  transition: bottom 0.4s;
}

.swiper-slide__feedback-container {
  padding: 60px 62px 55px 62px;
  background-color: #1c3374;
  filter: drop-shadow(0px 0px 40px rgba(28, 51, 116, 0.2));
  display: grid;
  grid-template-columns: auto 1fr;
  max-width: 560px;
  margin: 0 auto;
}
@media (max-width: 1199px) {
  .swiper-slide__feedback-container {
    max-width: 566px;
    padding: 60px 64px 56px 64px;
  }
}
@media (max-width: 767px) {
  .swiper-slide__feedback-container {
    max-width: 226px;
    padding: 40px 25px;
    row-gap: 20px;
  }
}

.swiper-slide__feedback-container picture {
  grid-column: 1;
  grid-row: 1/3;
}
@media (max-width: 767px) {
  .swiper-slide__feedback-container picture {
    grid-row: 1;
  }
}

.swiper-slide__feedback-container img {
  margin-right: 28px;
}

.swiper-slide__feedback-container span {
  display: block;
  grid-column: 2;
  color: #ffffff;
  font-size: 20px;
  font-weight: 700;
  line-height: 20px;
  margin-bottom: 17px;
}
@media (max-width: 767px) {
  .swiper-slide__feedback-container span {
    margin-bottom: 0;
    align-self: center;
  }
}

.swiper-slide__feedback-container p {
  grid-column: 2;
  color: #ffffff;
  font-size: 16px;
  font-weight: 400;
  line-height: 140%;
  margin: 0;
}
@media (max-width: 767px) {
  .swiper-slide__feedback-container p {
    text-align: center;
    grid-column: 1/3;
    grid-row: 2;
  }
}

